<template>
  <div id="loginWindow" class="loginPage">
    <h1 class="title">登录</h1>
    <el-form ref="user" :model="user" :rules="loginRules">
      <el-form-item prop="username">
        <el-input v-model="user.username" placeholder="请输入账号"></el-input>
      </el-form-item>
      <el-form-item prop="password">
        <el-input v-model="user.password" type="password" placeholder="请输入密码"></el-input>
      </el-form-item>
      <!-- <el-form-item prop="verify" :rules="[{ required: true, message: '请输入验证码', trigger: 'blur' }]">
				<el-input v-model="user.verify" placeholder="请输入验证码"></el-input>
      </el-form-item>-->
      <el-form-item>
        <el-button type="primary" style="width:45%">重置</el-button>
        <el-button
          type="primary"
          style="margin-left:20px;width:45%"
          @click.native.prevent="handleLogin"
        >登录</el-button>
      </el-form-item>
    </el-form>
    <!-- <el-row>
			<el-col :span="8">
				<el-input id="name" v-model="name" placeholder="请输入帐号"> <template slot="prepend">帐号</template>
				</el-input>
			</el-col>
		</el-row>
		<el-row>
			<el-col :span="8">
				<el-input id="password" v-model="password" type="password" placeholder="请输入密码"> <template
						slot="prepend">密码</template> </el-input>
			</el-col>
		</el-row>
		<el-row>
			<el-col :span="8">
				<el-button id="login" v-on:click="check" style="width:100%" type="primary">登录</el-button>
			</el-col>
    </el-row>-->
  </div>
</template>

<style scoped>
.loginPage {
  position: absolute;
  top: 10%;
  left: 50%;
  margin-top: 70px;
  margin-left: -175px;
  width: 400px;
  min-height: 350px;
  padding: 0px 20px 20px;
  border-radius: 8px;
  box-sizing: border-box;
  /* box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1); */
  box-shadow: 2px 2px 12px rgba(0, 0, 0, 0.12), 0 0 50px rgba(0, 0, 0, 0.04);
  background-color: white;
}

.title {
  /* color: red; */
  text-align: center;
}
</style>

<script>
//导入判断用户名方法
import { isvalidUsername } from "@/utils/validate";

export default {
  data() {
    const validateUsername = (rule, value, callback) => {
      if (!isvalidUsername(value)) {
        callback(new Error("请输入正确的用户名"));
      } else {
        callback();
      }
    };
    const validatePass = (rule, value, callback) => {
      if (value.length < 5) {
        callback(new Error("密码不能小于5位"));
      } 
      if (!isvalidUsername(value)) {
        // callback(new Error("请输入正确的密码"));
        alert("请输入正确的密码")
      }else {
        callback();
      }
    };
    return {
      user: {
        username: "admin",
        password: "admin"
        // verify: ""
      },
      loginRules: {
        username: [
          { required: true, trigger: "blur", validator: validateUsername }
        ],
        password: [{ required: true, trigger: "blur", validator: validatePass }]
      }
    };
  },
  methods: {
    handleLogin() {
      //$refs父组件有ref=user子组件下的所有特性
      //箭头函数,参数为valid
      this.$refs.user.validate(valid => {
        if (valid) {
          //   this.loading = true;
          this.$router.push({ path: this.redirect || "/" });
        } else {
          console.log("error submit!!");
          this.loading=false;
          return false;
        }
      });
    }
  }
};
</script>




